.paginationjs {
  display: flex;
  line-height: 1.6;
  font-family: Marmelad, 'Lucida Grande', Arial, 'Hiragino Sans GB', Georgia,
    sans-serif;
  font-size: 22px;
  box-sizing: initial;
  font-weight: bolder;
  margin: 20px auto;
}

.paginationjs:after {
  display: table;
  content: ' ';
  clear: both;
}

.paginationjs .paginationjs-pages {
  float: left;
  margin-left: 10px;
}

.paginationjs .paginationjs-pages ul {
  float: left;
  margin: 0;
  padding: 0;
}

.paginationjs .paginationjs-go-button,
.paginationjs .paginationjs-go-input,
.paginationjs .paginationjs-size-changer {
  margin-left: 10px;
  float: left;
  font-size: 14px;
}

.paginationjs .paginationjs-pages li {
  float: left;
  border: 1px solid transparent;
  border-right: none;
  list-style: none;
  padding: 6px;
}

.paginationjs .paginationjs-pages li > a {
  min-width: 30px;
  height: 28px;
  line-height: 28px;
  display: block;
  background: #fff;
  font-size: 14px;
  color: #333;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
}

.paginationjs .paginationjs-pages li > a:hover {
  background: #ff6b08;
  border-radius: 6px;
}

.paginationjs .paginationjs-pages li.active {
  border: none;
}

.paginationjs .paginationjs-pages li.active > a {
  height: 30px;
  line-height: 30px;
  background: #ff6b08;
  color: #fff;
  cursor: default;
  border-radius: 6px;
}

.paginationjs .paginationjs-pages li.disabled > a {
  opacity: 0.3;
  cursor: default;
}

.paginationjs .paginationjs-pages li.disabled > a:hover {
  background: none;
}

.paginationjs .paginationjs-pages li:first-child,
.paginationjs .paginationjs-pages li:first-child > a {
  background: #f7f7f7;
  border-radius: 6px;
}

.paginationjs .paginationjs-pages li:last-child {
  background: #f7f7f7;
  border-radius: 6px;
}

.paginationjs .paginationjs-pages li:last-child > a {
  background: #f7f7f7;
  border-radius: 6px;
}

.paginationjs .paginationjs-size-changer > select {
  height: 28px;
  background: #fff;
  border-radius: 3px;
  border: 1px solid #aaa;
  padding: 0;
  font-size: 14px;
  text-align: center;
  vertical-align: baseline;
  outline: 0;
  box-shadow: none;
  box-sizing: initial;
}

.paginationjs .paginationjs-go-input > input[type='text'] {
  width: 30px;
  height: 28px;
  background: #fff;
  border-radius: 3px;
  border: 1px solid #aaa;
  padding: 0;
  font-size: 14px;
  text-align: center;
  vertical-align: baseline;
  outline: 0;
  box-shadow: none;
  box-sizing: initial;
}

.paginationjs .paginationjs-go-button > input[type='button'] {
  min-width: 40px;
  height: 30px;
  line-height: 28px;
  background: #fff;
  border-radius: 3px;
  border: 1px solid #aaa;
  text-align: center;
  padding: 0 8px;
  font-size: 14px;
  vertical-align: baseline;
  outline: 0;
  box-shadow: none;
  color: #333;
  cursor: pointer;
  vertical-align: middle\9;
}

.paginationjs .paginationjs-go-button > input[type='button']:hover {
  background-color: #f8f8f8;
}

.paginationjs .paginationjs-nav {
  float: left;
  height: 30px;
  line-height: 30px;
  font-size: 14px;
}

.paginationjs.paginationjs-small {
  font-size: 12px;
}

.paginationjs.paginationjs-small .paginationjs-pages li > a {
  min-width: 26px;
  height: 24px;
  line-height: 24px;
  font-size: 12px;
}

.paginationjs.paginationjs-small .paginationjs-pages li.active > a {
  height: 26px;
  line-height: 26px;
}

.paginationjs.paginationjs-small .paginationjs-size-changer {
  font-size: 12px;
}

.paginationjs.paginationjs-small .paginationjs-size-changer > select {
  height: 24px;
  font-size: 12px;
}

.paginationjs.paginationjs-small .paginationjs-go-input {
  font-size: 12px;
}

.paginationjs.paginationjs-small .paginationjs-go-input > input[type='text'] {
  width: 26px;
  height: 24px;
  font-size: 12px;
}

.paginationjs.paginationjs-small .paginationjs-go-button {
  font-size: 12px;
}

.paginationjs.paginationjs-small
  .paginationjs-go-button
  > input[type='button'] {
  min-width: 30px;
  height: 26px;
  line-height: 24px;
  padding: 0 6px;
  font-size: 12px;
}

.paginationjs.paginationjs-small .paginationjs-nav {
  height: 26px;
  line-height: 26px;
  font-size: 12px;
}

.paginationjs.paginationjs-big {
  font-size: 16px;
}

.paginationjs.paginationjs-big .paginationjs-pages li > a {
  min-width: 36px;
  height: 34px;
  line-height: 34px;
  font-size: 16px;
}

.paginationjs.paginationjs-big .paginationjs-pages li.active > a {
  height: 36px;
  line-height: 36px;
}

.paginationjs.paginationjs-big .paginationjs-size-changer {
  font-size: 16px;
}

.paginationjs.paginationjs-big .paginationjs-size-changer > select {
  height: 34px;
  font-size: 16px;
}

.paginationjs.paginationjs-big .paginationjs-go-input {
  font-size: 16px;
}

.paginationjs.paginationjs-big .paginationjs-go-input > input[type='text'] {
  width: 36px;
  height: 34px;
  font-size: 16px;
}

.paginationjs.paginationjs-big .paginationjs-go-button {
  font-size: 16px;
}

.paginationjs.paginationjs-big .paginationjs-go-button > input[type='button'] {
  min-width: 50px;
  height: 36px;
  line-height: 34px;
  padding: 0 12px;
  font-size: 16px;
}

.paginationjs.paginationjs-big .paginationjs-nav {
  height: 36px;
  line-height: 36px;
  font-size: 16px;
}

.paginationjs > :first-child {
  margin-left: 0;
}

